<template>
  <div>
    <form id="sign-in" class='component' v-on:submit.prevent="signIn" method="post">
      <h3>Returning User? Sign in</h3>
      
      <input id='in-username' v-model.trim='username' type='text' name='username' placeholder="Username">
      <input id='in-password' v-model.trim='password' type='password' name='password' placeholder="Password">
      <input type='submit' value='Sign In' class="btn btn-primary">

      <div v-if='errors.length' class="error-message" style="width: 250px;">
        <b>Please correct the following error(s):</b>
        <ul>
          <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
        </ul>
      </div>

    </form>
    
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "SignIn",

  data() {
    return {
      errors: [],
      username: "",
      password: "",
      intervalID: null
    }
  },

  methods: {
    signIn: function() {
      this.errors = [];

      const bodyContent = { username: this.username, password: this.password };
        axios
          .post("/api/user/login", bodyContent)
          .then((res) => {
            // handle success
            eventBus.$emit('signin-success', res.data.username);
          })
          .catch(err => {
            // handle error
            this.errors.push(err.response.data.message)
          })
          .then(() => {
            // always executed
            this.resetForm();
            this.clearMessages();
          });
    },

    resetForm: function() {
      this.username = ""
      this.password = ""
    },

    clearMessages: function() {
      if (this.intervalID) {
        clearInterval(this.intervalID);
      }
      this.intervalID = setInterval(() => {
        this.errors = [];
      }, 5000);
    }
  }
}
</script>