<template>
  <div id="delete-user" class="component">
    <button v-on:click="deleteUser" class = "btn btn-danger">Delete User</button>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "DeleteUser",
  
  methods: {
    deleteUser: function() {
      const username = this.$cookie.get('fritter-auth')
      axios.delete('api/user/')
        .then(() => {
          // handle success
          eventBus.$emit('delete-user-success', {username: username});
        })
        .catch(() => {
          // Still sign User out so they have to sign in again.
          eventBus.$emit('delete-user-success', {username: username});
        })
    }
  }
}
</script>