<template>
  <div>
    <form id="change-username" v-on:submit.prevent="changeUsername">
      <h3>Change Username</h3>
      <div>
        <input id="username" name="username" v-model="username" placeholder="new username">
      </div>
      <input type="submit" value="Change Username" class="btn btn-primary">

      <div v-if='errors.length' class="error-message" style="width: 250px;">
        <b>Please correct the following error(s):</b>
        <ul>
          <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
        </ul>
      </div>

    </form>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from '../main.js'


export default {
  name: "ChangeUsername",
  props: [],
  data() {
    return {
    username: "",
    errors: [],
    intervalID: null
    };
  },

  methods: {
    changeUsername() {
      this.errors = [];

      axios
        .put('/api/user/username', {username: this.username})
        .then(() => {
          eventBus.$emit("change-username-success", {newUsername: this.username});
        })
        .catch((err) => {
          this.errors.push(err.response.data.message)
        })
        .then(() => {
          // always executed
          this.resetForm();
          this.clearMessages();
        });
    },

    resetForm: function() {
      this.username = ""
    },

    clearMessages: function() {
      if (this.intervalID) {
        clearInterval(this.intervalID);
      }
      this.intervalID = setInterval(() => {
        this.errors = [];
      }, 5000);
    }

  },
};
</script>

