<template>
  <div>
    <form id="sign-up" class='component' v-on:submit.prevent="signUp" method="post">
      <h3>Join Fritter!</h3>

      <input id='up-username' v-model.trim='username' type='text' name='username' placeholder="Username">
      <input id='up-password' v-model.trim='password' type='password' name='password' placeholder="Password">
      <input type='submit' value='Sign Up' class="btn btn-primary">

      <div v-if='errors.length' class="error-message" style="width: 250px;">
        <b>Please correct the following error(s):</b>
        <ul>
          <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
        </ul>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from "../main";

export default {
  name: "SignUp",

  data() {
    return {
      errors: [],
      username: "",
      password: "",
      intervalID: null
    }
  },

  methods: {
    signUp: function() {
      this.errors = [];

      const bodyContent = { username: this.username, password: this.password};
        axios
          .post("/api/user/create", bodyContent)
          .then((res) => {
            // handle success
            eventBus.$emit('signup-success', res.data.username);
          })
          .catch(err => {
            // handle error
            this.errors.push(err.response.data.message);
          })
          .then(() => {
            // always executed
            this.resetForm();
            this.clearMessages();
          });
    },

    resetForm: function() {
      this.username = ""
      this.password = ""
    },

    clearMessages: function() {
      if (this.intervalID) {
        clearInterval(this.intervalID);
      }
      this.intervalID = setInterval(() => {
        this.errors = [];
      }, 5000);
    }
  }
}
</script>