<template>
  <div id="user-settings">
    <div v-if="isSignedIn" class="form-container">
      <ChangeUsername/>
      <br>
      <ChangePassword/>
      <br>
      <div class = "sidebysidebuttons">
        <SignOut/>
        <DeleteUser/>
      </div>
    </div>
    <div v-else class="form-container">
      <SignIn/>
      <br>
      <SignUp/>
    </div>
    <div v-if='messages.length' class="success-message" style="text-align:center;">
      <div v-for='message in messages' v-bind:key='message.id'><b>{{ message }}</b></div>
    </div>
  </div>
</template>

<script>
import SignIn from "./SignIn.vue";
import SignOut from "./SignOut.vue";
import SignUp from "./SignUp.vue";
import DeleteUser from "./DeleteUser.vue"
import ChangeUsername from "./ChangeUsername.vue";
import ChangePassword from "./ChangePassword.vue";
import { eventBus } from "../main";

export default {
  name: "UserSettings",

  components: {
    SignIn,
    SignOut,
    SignUp,
    ChangeUsername,
    ChangePassword,
    DeleteUser
  },

  data() {
    return {
      isSignedIn: false,
      messages: []
    };
  },

  created: function() {
    let authenticated = this.$cookie.get('fritter-auth');
    if (authenticated) {
      this.isSignedIn = true;
    }

    eventBus.$on("signin-success", (userName) => {
      this.$cookie.set('fritter-auth', userName);
      this.isSignedIn = true;
      this.messages.push("You have been signed in!");
      this.clearMessages();

    });

    eventBus.$on("signup-success", (userName) => {
      this.$cookie.set('fritter-auth', userName);
      this.isSignedIn = true;
      this.messages.push("You have successfully created an account!");
      this.clearMessages();

    });
    
    eventBus.$on("signout-success", () => {
      this.$cookie.set('fritter-auth', '');
      this.isSignedIn = false;
      this.messages.push("You have been signed out!");
      this.clearMessages();
    });

    eventBus.$on("change-username-success", (res) => {
      this.$cookie.set('fritter-auth', res.newUsername);
      this.isSignedIn = true;
      this.messages.push("You have successfully changed your username!");
      this.clearMessages();

    });

    eventBus.$on("change-password-success", () => {
      this.isSignedIn = true;
      this.messages.push("You have successfully changed your password!");
      this.clearMessages();

    });

    eventBus.$on("delete-user-success", () => {
      this.$cookie.set('fritter-auth', '');
      this.isSignedIn = false;
      this.messages.push("Your account has been deleted!");
      this.clearMessages();
    });

  },
  methods: {
    resetForm: function() {
      this.username = ""
      this.password = ""
    },

    clearMessages: function() {
      setInterval(() => {
        this.messages = [];
      }, 5000);
    }
  }
};
</script>