<template>
  <div>
    <form id="change-password" v-on:submit.prevent="changePassword">
      <h3>Change Password</h3>
      <div>
        <input id="password" name="password" type ="password" v-model="password" placeholder="new password">
      </div>
      <input type="submit" value="Change Password" class="btn btn-primary">

      <div v-if='errors.length' class="error-message" style="width: 250px;">
        <b>Please correct the following error(s):</b>
        <ul>
          <li v-for='error in errors' v-bind:key='error.id'>{{ error }}</li>
        </ul>
      </div>

    </form>
  </div>
</template>

<script>
import axios from "axios";
import { eventBus } from '../main.js'


export default {
  name: "ChangePassword",
  props: [],
  data() {
    return {
    password: "",
    errors: [],
    intervalID: null
    };
  },
  created: function() {},
  methods: {
    changePassword() {
      this.errors = []

      axios.put('/api/user/password', {password: this.password})
        .then(() => {
          eventBus.$emit("change-password-success");
        })
        .catch((err) => {
          this.errors.push(err.response.data.message)
        })
        .then(() => {
            // always executed
            this.resetForm();
            this.clearMessages();
        });
    },

    resetForm: function() {
      this.password = ""
    },

    clearMessages: function() {
      if (this.intervalID) {
        clearInterval(this.intervalID);
      }
      this.intervalID = setInterval(() => {
        this.errors = [];
      }, 5000);
    }
  },
};
</script>

